export default {
  bind(el, _, vnode) {
    const [dialogD, dialogElement] = [el.querySelector('.el-dialog'), el.querySelector('.el-dialog__header')];
    dialogElement.style.cssText += ';cursor:move;';
    dialogD.style.cssText += ';top:0px;';

    const getDialogSys = (function () {
      return (dom, attr) =>
        window.document.currentStyle ? dom.currentStyle[attr] : getComputedStyle(dom, false)[attr];
    })();

    dialogElement.onmousedown = (e) => {
      const [dialogW, dialogH] = [dialogD.offsetWidth, dialogD.offsetHeight];
      const [dialogX, dialogY] = [e.clientX - dialogElement.offsetLeft, e.clientY - dialogElement.offsetTop];
      const minDialogLeft = dialogD.offsetLeft + 280;
      const maxDialogLeft = document.body.clientWidth - dialogD.offsetLeft - dialogW + 280;
      const minDialogTop = dialogD.offsetTop;
      const maxDialogTop = window.innerHeight - dialogD.offsetTop - dialogH;
      let [styL, styT] = [getDialogSys(dialogD, 'left'), getDialogSys(dialogD, 'top')];
      if (styL.includes('%')) {
        styL = +document.body.clientWidth * (+styL.replace(/%/g, '') / 100);
        styT = +document.body.clientHeight * (+styT.replace(/%/g, '') / 100);
      } else {
        styL = +styL.replace(/\px/g, '');
        styT = +styT.replace(/\px/g, '');
      }
      document.onmousemove = function (e) {
        let [l, t] = [e.clientX - dialogX, e.clientY - dialogY];
        if (-l > minDialogLeft) {
          l = -minDialogLeft;
        } else if (l > maxDialogLeft) {
          l = maxDialogLeft;
        }
        if (-t > minDialogTop) {
          t = -minDialogTop;
        } else if (t > maxDialogTop) {
          t = maxDialogTop;
        }
        dialogD.style.cssText += `;left:${l + styL}px;top:${t + styT}px;`;
        vnode.child.$emit('dragDialog');
      };
      document.onmouseup = function () {
        document.onmousemove = null;
        document.onmouseup = null;
      };
    };
  },
};
