  data() {
    let dateValidate = (rule, val, callback) => {
      if (val.length == 0) {
        callback();
      }
      if (val) {
        let sTime = val[0];
        let startTime = new Date(sTime).getTime();
        let eTime = val[1];
        let endTime = new Date(eTime).getTime();
        let result = endTime - startTime < 31 * 24 * 3600 * 1000;
        if (result) {
          callback();
        } else {
          callback(new Error('The time period cannot exceed 31 days'));
        }
      }
      callback();
    };
    return {
      pickerOptions: {
        onPick: ({ maxDate, minDate }) => {
          this.selectDate = minDate.getTime();
          if (maxDate) {
            this.selectDate = '';
          }
        },
        disabledDate: (time) => {
          if (this.selectDate !== '') {
            const one = 30 * 24 * 3600 * 1000;
            const minTime = this.selectDate - one;
            const maxTime = this.selectDate + one;
            return time.getTime() < minTime || time.getTime() > maxTime;
          }
        },
      },
      isCreateSuccess: false,
      dialogSearchForm: {
        shortCode: '%',
        organizationName: '%',
      },
      submitLoading: false,
      reasonDialogVisible: false,
      addDialogVisible: false,
      dialogSearchLoading: false,
      addPageColumns: [
        {
          label: this.$t('BUC.revenueSettlement.organizationShortCode'),
          prop: 'shortCode',
          align: 'left',
        },
        {
          label: this.$t('BUC.revenueSettlement.organizationName'),
          prop: 'organizationName',
          align: 'left',
        },
        {
          label: 'Support Settlement',
          prop: 'hasProfile',
          align: 'left',
        },
        {
          label: this.$t('BUC.revenueSettlement.amount'),
          prop: 'amount',
          align: 'left',
          'min-width': '110',
          __slotName: 'amount',
          'render-header': (h, { column }) => {
            return h('div', [
              h('span', column.label),
              h(
                'el-tooltip',
                {
                  props: {
                    placement: 'top',
                    'popper-class': 'tool-tip',
                    content:
                      'The system supports partial revenue settlement. When initiates a revenue settlement transaction for an organization, you can specify the expected net revenue amount transferred to the Working Account of the organization after settlement.',
                  },
                },
                [
                  h('svg-icon', {
                    props: { 'icon-class': 'questionMark' },
                    class: 'svgIcon',
                    style: 'color:#409eff;margin-left:5px;',
                  }),
                ]
              ),
            ]);
          },
        },
        {
          label: this.$t('BUC.revenueSettlement.operation'),
          prop: 'operation',
          align: 'left',
          __slotName: 'opera',
        },
      ],
      addPageIsSearch: false,
      isInitiate: false,
      isListPage: true,
      paginationConfig: {
        pageIndex: 1,
        pageSize: 10,
      },
      addDialogPaginationConfig: {
        pageIndex: 1,
        pageSize: 5,
        layout: 'total, prev, pager, next',
      },
      titleConfig: {
        title: this.$t('BUC.revenueSettlement.pageTitle'),
        desc: this.$t('BUC.revenueSettlement.desc'),
