      addDialogIsSearch: false,
      showCreateList: false,
      checkedList: [],
      checkedList2: [],
      addDialogColumns: [
        {
          label: this.$t('BUC.revenueSettlement.shortCode'),
          prop: 'shortCode',
          align: 'left',
          width: '200',
        },
        {
          label: this.$t('BUC.revenueSettlement.organizationName'),
          prop: 'organizationName',
          align: 'left',
        },
        {
          label: 'Support Settlement',
          prop: 'hasProfile',
          align: 'left',
        },
        {
          label: this.$t('BUC.revenueSettlement.operation'),
          prop: 'operation',
          align: 'left',
          __slotName: 'opera',
          width: '150',
        },
      ],
      columns: [
        {
          label: this.$t('BUC.revenueSettlement.planId'),
          prop: 'planOrderId',
          align: 'left',
          'sort-method': (a, b) => {
            return a.planId > b.planId ? 1 : -1;
          },
