        },
        {
          label: this.$t('BUC.revenueSettlement.status'),
          prop: 'statusName',
          align: 'left',
          __slotName: 'taskStatus',
          width: '220',
        },
        {
          label: this.$t('BUC.revenueSettlement.initiationTime'),
          prop: 'createTimeNew',
          align: 'left',
          width: '170',
          'sort-method': (a, b) => {
            return a.initiationTime > b.initiationTime ? 1 : -1;
          },
        },
        {
          label: this.$t('BUC.revenueSettlement.completionTime'),
          prop: 'endTimeNew',
          align: 'left',
          width: '170',
          'sort-method': (a, b) => {
            return a.completionTime > b.completionTime ? 1 : -1;
          },
        },
        {
          label: this.$t('BUC.revenueSettlement.operator'),
          prop: 'lastUpdateOpername',
          align: 'left',
          width: '110',
          'sort-method': (a, b) => {
            return a.operator > b.operator ? 1 : -1;
          },
        },
        {
          label: this.$t('BUC.revenueSettlement.expectedNetRevenueAmount'),
          prop: 'expectedNetAmount',
          align: 'left',
          width: '240',
        },
        {
          label: this.$t('BUC.revenueSettlement.currency'),
          prop: 'currency',
          align: 'left',
          width: '116',
          'sort-method': (a, b) => {
            return a.currency > b.currency ? 1 : -1;
          },
        },
        {
          label: this.$t('BUC.revenueSettlement.fundingSource'),
          prop: 'fundingSourceValue',
          align: 'left',
          width: '140',
        },
        { label: this.$t('BUC.revenueSettlement.triggerModel'), prop: 'planTypeName', align: 'left', width: '140' },
        {
          label: this.$t('BUC.revenueSettlement.operation'),
          prop: 'operation',
          align: 'left',
          fixed: 'right',
          width: 150,
          __slotName: 'opera',
        },
      ],
    };
  },
  computed: {
    formConfigArray() {
      return [
        {
          label: this.$t('BUC.revenueSettlement.planId'),
          key: 'planId',
          comp: 'el-input',
          bind: {
            placeholder: 'Please enter',
            id: 'planId',
          },
        },
        {
          label: this.$t('BUC.revenueSettlement.status'),
          key: 'status',
          comp: 'el-select',
          options: this.searchOptions.statusList,
          bind: {
            placeholder: 'Please select',
            id: 'status',
          },
        },
        {
          formItemClass: 'org-short-code',
          label: this.$t('BUC.commissionRollDown.orgShortCode'),
          key: 'orgShortCode',
          prop: 'orgShortCode',
          comp: 'el-input',
          bind: {
            placeholder: this.$t('common.placeholder.select'),
            clearable: true,
          },
          slots: [
            {
              name: 'append',
              comp: 'el-link',
              class: 'short-code-icon',
              bind: {
                icon: 'el-icon-more',
                underline: false,
              },
              event: {
                click: () => {
                  this.searchVisible = true;
                },
              },
            },
          ],
        },
        {
          label: this.$t('BUC.revenueSettlement.triggerModel'),
          key: 'triggerModel',
          comp: 'el-select',
          options: this.searchOptions.triggerModelList,
          bind: {
            placeholder: 'Please select',
            id: 'triggerModel',
          },
        },
        {
          formItemClass: 'form-special-date-box',
          label: this.$t('BUC.revenueSettlement.initiationTime'),
          key: 'initiationTime',
          prop: 'initiationTime',
          comp: 'el-date-picker',
          bind: {
            placeholder: this.$t('common.placeholder.enter'),
            type: 'daterange',
            format: 'dd/MM/yyyy',
            'range-separator': '-',
            'start-placeholder': 'Start',
            'end-placeholder': 'End',
            'picker-options': this.pickerOptions,
          },
        },
        {
          formItemClass: 'form-special-date-box',
          label: this.$t('BUC.revenueSettlement.settlementEndTime'),
          key: 'settlementEndTime',
          comp: 'el-date-picker',
          bind: {
            placeholder: this.$t('common.placeholder.enter'),
            type: 'daterange',
            format: 'dd/MM/yyyy',
            'range-separator': '-',
            'start-placeholder': 'Start',
            'end-placeholder': 'End',
          },
        },
        {
          label: this.$t('BUC.revenueSettlement.fundingSource'),
          key: 'fundingSource',
          comp: 'el-select',
          options: this.searchOptions.fundingSourceList,
          bind: {
            placeholder: 'Please select',
            id: 'fundingSource',
          },
        },
      ];
    },
    dialogFormConfigArray() {
      return [
        {
          label: this.$t('BUC.revenueSettlement.shortCode'),
          key: 'shortCode',
          comp: 'el-input',
          bind: {
            placeholder: 'Please enter',
            id: 'shortCode',
          },
        },
        {
          label: this.$t('BUC.revenueSettlement.organizationName'),
          key: 'organizationName',
          comp: 'el-input',
          bind: {
            placeholder: 'Please enter',
            id: 'organizationName',
          },
        },
      ];
    },
    statusStyle() {
      return (taskStatus) => {
        switch (taskStatus) {
          case 'Terminated':
            return { 'background-color': `${this.GlobalStyleVariables.fillStrongColor}` };
          case 'Cancelled':
            return { 'background-color': `${this.GlobalStyleVariables.fillStrongColor}` };
          case 'Completed':
            return { 'background-color': `${this.GlobalStyleVariables.statusSuccessColor}` };
          case 'Running':
            return { 'background-color': `${this.GlobalStyleVariables.statusNoticeColor}` };
          case 'Prepared':
            return { 'background-color': `${this.GlobalStyleVariables.statusColorwarning}` };
          case 'Second Settlement Failed':
            return { 'background-color': `${this.GlobalStyleVariables.statusColorwarning}` };
          case 'First Settlement Failed':
            return { 'background-color': `${this.GlobalStyleVariables.statusColorwarning}` };
          default:
            return;
        }
      };
    },
  },
  created() {
    this.getSearchOptions();
  },
  methods: {
    isSelectable(row) {
      if (row.hasProfile === 'Yes') {
        return true;
      } else if (row.hasProfile === 'No') {
        return false;
      }
    },
    async getSearchOptions() {
      let typeData = ['SettlementProcessStatus', 'SettlementTriggerModel', 'FundingSource'];
      let option = [
        this.searchOptions.statusList,
        this.searchOptions.triggerModelList,
        this.searchOptions.fundingSourceList,
      ];
      for (let i = 0; i < typeData.length; i++) {
        let res = await queryDict({ type: typeData[i] });
        if (res.header && res.header.success) {
          res.dictList.forEach((item) => {
            option[i].push({
              label: item.value,
              value: item.key,
            });
          });
        }
      }
    },
    addDialogConfirm() {
      this.checkedList.forEach((item) => {
        if (this.checkedList2.length === 0) {
          this.checkedList2.push(item);
        } else {
          let isRepeat = false;
          this.checkedList2.forEach((item2) => {
            if (item2.organizationId === item.organizationId) {
              isRepeat = true;
            }
          });
          if (!isRepeat) {
            this.checkedList2.push(item);
          }
        }
      });
      this.checkedList = [];
      this.addDialogVisible = false;
      this.dialogSearchForm.shortCode = '%';
      this.dialogSearchForm.organizationName = '%';
      if (this.checkedList2.length > 0) {
        this.showCreateList = true;
        this.isSubmitBtnActive = false;
      } else {
        this.showCreateList = false;
        this.isSubmitBtnActive = true;
      }
      this.addPageIsSearch = true;
    },
    handleSelectionChange(val) {
      this.checkedList = val;
    },
    getAddDialogTableData() {
      return new Promise((resolve) => {
        let data = {
          orgShortCode: this.dialogSearchForm.shortCode,
          orgName: this.dialogSearchForm.organizationName,
          pageIndex: this.addDialogPaginationConfig.pageIndex,
          pageSize: this.addDialogPaginationConfig.pageSize,
        };
        queryOrgAndProfile(data)
          .then((res) => {
            if (res.header && res.header.success) {
              this.addDialogIsSearch = false;
              this.dialogSearchLoading = false;
              if (res.list) {
                res.totalRecords = res.total;
                let listNew = res.list;
                if (res.list[0]) {
                  if (res.list[0].organizationDTO) {
                    listNew = [];
                    res.list.forEach((item) => {
                      listNew.push({ ...item.organizationDTO, hasProfile: item.hasProfile });
                    });
                  }
                }
                res.list = listNew;
                res.list.forEach((item) => {
                  if (item.hasProfile === true) {
                    item.hasProfile = 'Yes';
                  } else if (item.hasProfile === false) {
                    item.hasProfile = 'No';
                  }
                });
              } else {
                res.list = [];
              }
              resolve(res);
            }
          })
          .finally(() => {
            this.dialogSearchLoading = false;
            this.addDialogIsSearch = false;
          });
      });
    },
    submitCreate() {
      this.$confirm(
        'Initiating revenue settlement on the portal has performance risks and may fail to be completed due to timeout. You are advised to initiate the system task plan Org Revenue Settlement Process. Are you sure you want to continue?',
        'Confirm',
        {
          confirmButtonText: 'Confirm',
          cancelButtonText: 'Cancel',
          closeOnClickModal: false,
          type: 'warning',
          customClass: 'box-logout',
        }
      )
        .then(async () => {
          let submitList = [];
          this.checkedList2.forEach((item) => {
            submitList.push({
              orgId: item.organizationId,
              amount: item.expectedRevenueAmount || '',
            });
          });
          let data = { isAllOrg: false, list: submitList };
          let res = await execOrgAutoSweep(data);
          if (res.header && res.header.success) {
            this.checkedList2.forEach((item) => {
              item.expectedRevenueAmount = '';
            });
            this.checkedList2 = [];
            this.isCreateSuccess = true;
            this.isListPage = false;
            this.isInitiate = false;
          }
        })
        .catch(() => {
          return;
        });
    },
    getAddPageTableData() {
      return new Promise((resolve) => {
        this.addPageIsSearch = false;
        let res = {
          header: { success: true },
          list: this.checkedList2,
        };
        resolve(res);
      });
    },
    toFirstStep() {
      this.checkedList2.forEach((item) => {
        item.expectedRevenueAmount = '';
      });
      this.isListPage = true;
      this.isInitiate = false;
      this.isCreateSuccess = false;
      this.checkedList2 = [];
      this.addDialogConfirm();
    },
    toFirstStepAsk() {
      this.$confirm(
        'All data you have entered/selected in this page will be cancelled',
        'Are you sure to return Revenue Settlement?',
        {
          confirmButtonText: 'Confirm',
          cancelButtonText: 'Cancel',
          closeOnClickModal: false,
          type: 'warning',
          customClass: 'box-logout',
        }
      )
        .then(() => {
          this.checkedList2.forEach((item) => {
            item.expectedRevenueAmount = '';
          });
          this.isListPage = true;
          this.isInitiate = false;
          this.isCreateSuccess = false;
          this.checkedList2 = [];
          this.addDialogConfirm();
        })
        .catch(() => {
          return;
        });
    },
    addBtnClick() {
      this.addDialogVisible = true;
      this.addDialogIsSearch = true;
    },
    search() {
      this.$refs['settlementFormRef'].validate((valid) => {
        if (!valid) {
          return;
        }
        this.searchLoading = true;
        this.isSearch = true;
        this.paginationConfig = {
          pageIndex: 1,
          pageSize: 10,
        };
      });
    },
    reset() {
      this.$refs.settlementFormRef.resetFields();
      this.isSearch = true;
      this.searchLoading = true;
      this.paginationConfig.pageIndex = 1;
      this.paginationConfig.pageSize = 10;
    },
    handleDeleteClick(row) {
      this.$confirm('Are you sure you want to delete the Organization?', {
        confirmButtonText: 'Confirm',
        cancelButtonText: 'Cancel',
        closeOnClickModal: false,
        type: 'warning',
        customClass: 'box-logout',
      })
        .then(() => {
          this.checkedList2.forEach((item) => {
            if (item.organizationId === row.organizationId) {
              this.checkedList2.splice(this.checkedList2.indexOf(item), 1);
              item.expectedRevenueAmount = '';
            }
          });
          this.addDialogConfirm();
        })
        .catch(() => {
          return;
        });
    },
    getOrgShortCode() {
      return new Promise((resolve) => {
        let params = {
          shortCode: this.settlementForm.orgShortCode || '',
        };
        getOrganizationList(params).then((res) => {
          this.searchLoading = false;
          if (res.header && res.header.success && res.organizationInfo) {
            resolve(res.organizationInfo.organizationId);
          } else {
            resolve('');
          }
        });
      });
    },
    async getTableData() {
      let orgId;
      if (this.settlementForm.orgShortCode) {
        orgId = await this.getOrgShortCode();
        if (!orgId) {
          this.searchLoading = false;
          this.isSearch = false;
          return {
            header: {
              success: false,
            },
          };
        }
      }
      return new Promise((resolve) => {
        let initiationEarliestTime = dateFormat(this.settlementForm.initiationTime[0], 'YYYY-MM-DDTHH:mm:ss') || '';
        let initiationLatestTime = dateFormat(this.settlementForm.initiationTime[1], 'YYYY-MM-DDTHH:mm:ss') || '';
        let settlementStartTime = dateFormat(this.settlementForm.settlementEndTime[0], 'YYYY-MM-DDTHH:mm:ss') || '';
        let settlementEndTime = dateFormat(this.settlementForm.settlementEndTime[1], 'YYYY-MM-DDTHH:mm:ss') || '';
        let data = {
          planId: this.settlementForm.planId,
          statusCode: this.settlementForm.status,
          orgId,
          triggerModelCode: this.settlementForm.triggerModel,
          initiationEarliestTime,
          initiationLatestTime,
          settlementStartTime,
          settlementEndTime,
          fundingSourceCode: this.settlementForm.fundingSource,
          spQuery: false,
          pageNo: this.paginationConfig.pageIndex,
          pageSize: this.paginationConfig.pageSize,
        };
        getRevenueSettlementList(data).then((res) => {
          this.searchLoading = false;
          this.isSearch = false;
          if (res.header && res.header.success) {
            res.list.forEach((item) => {
              item.createTimeNew = item.createTime ? dayjs(item.createTime).format('DD/MM/YYYY HH:mm:ss') : '';
              item.endTimeNew = item.endTime ? dayjs(item.endTime).format('DD/MM/YYYY HH:mm:ss') : '';
              if (item.lastUpdateOpername) {
                return;
              } else if (!item.lastUpdateOpername) {
                item.lastUpdateOpername = item.createOperName;
              }
            });
            res.totalRecords = res.total;
            resolve(res);
          }
        });
      });
    },
    toAddPage() {
      this.isInitiate = true;
      this.isListPage = false;
    },
    handleProcessClick(row) {
      this.openNewWindow({
        path: 'settlementProcess',
        query: { params: JSON.stringify(row) },
      });
    },
    handleDownloadClick(row) {
      let data = {
        planOrderId: row.planOrderId,
        exportType: 'Excel',
      };
      const url = `${process.env.VUE_APP_BASEAPI}/fintech/v1/settlement/revenue/org/downloadOrgRevenueReport`;
      FileUtil.downLoadFile(url, data, 'POST', true);
    },
    handleDetailClick(row) {
      this.openNewWindow({
        path: 'settlementDetail',
        query: { params: JSON.stringify(row) },
      });
    },
    toOrgDetail(row) {
      let params = {
        organizationId: row.organizationId,
        propShortCode: row.shortCode,
        identityId: row.organizationId,
      };
      this.openNewWindow({
        path: '/mainPage/identifierCenter/organizationInfo/organization/detail',
        query: { data: encodeURIComponent(JSON.stringify(params)) },
      });
    },
    viewReasonDialog() {
      this.reasonDialogVisible = true;
    },
    dialogReset() {
      this.$refs['addDialogFormRef'].resetFields();
      this.checkedList = [];
      this.addDialogIsSearch = true;
      this.dialogSearchLoading = true;
      this.addDialogPaginationConfig.pageIndex = 1;
    },
    dialogSearch() {
      this.dialogSearchLoading = true;
      this.addDialogIsSearch = true;
      this.addDialogPaginationConfig.pageIndex = 1;
    },
    addDialogCancel() {
      this.addDialogVisible = false;
      this.$refs['addDialogFormRef'].resetFields();
    },
    backToInitiate() {
      this.addDialogConfirm();
      this.isListPage = false;
      this.isInitiate = true;
      this.isCreateSuccess = false;
    },
  },
};
</script>

